"use client";

import React from "react";
import xImg from "@/public/images/icon-x.svg";
import Image from "next/image";
import { useGame } from "../_context/GameContext";

export default function WinScreen() {
  const { gameWinner } = useGame();
  return gameWinner ? (
    <div className="absolute h-[228px] w-full top-[220px] bottom-[220px] bg-semi-dark-navy transform">
      <div className="flex flex-col justify-center items-center">
        <p className="mt-[40px] mb-[16px] font-bold text-silver">YOU WON</p>
        <div className="flex items-center gap-x-[9px] mb-[24px]">
          <Image src={xImg} alt="icon x" className="w-[28px] h-[28px]" />
          <p className="text-light-blue font-bold text-[24px]">
            TAKES THE ROUND
          </p>
        </div>
        <div className="flex items-center gap-x-[16px]">
          <button className="text-[16px] font-bold h-[56px] w-[76px] rounded-[10px] bg-silver">
            QUIT
          </button>
          <button className="text-[16px] h-[56px] w-[146px] bg-light-yellow font-bold px-[17px] rounded-[10px]">
            NEXT ROUND
          </button>
        </div>
      </div>
    </div>
  ) : null;
}
